<template>
  <div class="gv-scuoladigitale-ricerca gv-inverted-color-scheme" id="gv-scuoladigitale-ricerca">
    <gv-title
      v-draggable
      title="Ricerca Tematica"
      :divId="'gv-scuoladigitale-ricerca'"
      :noClose="true"
      :collapsible="'gv-scuoladigitale-ricerca'"
      :width="'500px'"
    ></gv-title>
    <div class="gv-scuoladigitale-ricerca-body" id="gv-scuoladigitale-ricerca-body">
      <el-select v-model="ordini" size="mini" multiple collapse-tags placeholder="Seleziona">
        <el-option v-for="item in listaOrdini" :key="item.id" :value="item.id" :label="item.label">
        </el-option>
      </el-select>
      <div class="gv-scuoladigitale-ricerca-buttons">
        <el-button id="gv-scuoladigitale-ricerca-submit" type="primary" size="mini" @click="submit"
          >Conferma</el-button
        >
        <el-button id="gv-scuoladigitale-ricerca-cancel" type="primary" size="mini" @click="cancel"
          >Annulla</el-button
        >
        <el-button id="gv-scuoladigitale-ricerca-reset" type="primary" size="mini" @click="reset"
          >Reimposta</el-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';

import axios from 'axios';
import notification from '../util/notification';

import { Button, Form, FormItem, Select, Option, Notification } from 'element-ui';
Vue.use(Button);
Vue.use(Select);
Vue.use(Option);
import lang from 'element-ui/lib/locale/lang/it';
import locale from 'element-ui/lib/locale';
locale.use(lang);

export default {
  name: 'gv-scuoladigitale-ricerca',
  props: {
    idMap: String,
    selFogli: String,
    field: String,
  },
  data() {
    return {
      ordini: [],
      listaOrdini: [],
      parole: [],
      listaParole: [],
      config: {},
    };
  },
  computed: {},
  watch: {},
  async mounted() {
    // imposto la configurazione dopo il caricamento della config della mappa
    let response = await axios.get('/geoservices/REST/scuola/ordini');
    this.listaOrdini = await response.data.data;
  },
  methods: {
    submit() {},
    cancel() {},
    reset() {},
  },
};
</script>

<style scoped>
.gv-scuoladigitale-ricerca {
  position: absolute;
  left: 0;
  top: 0;
  margin-left: 10px;
  margin-top: 10px;
  z-index: 800;
}

.gv-scuoladigitale-ricerca-body {
  margin: 10px;
}

.gv-scuoladigitale-ricerca-label {
  display: inline-block;
  width: 150px;
}

.gv-scuoladigitale-ricerca-title {
  display: inline-block;
  font-weight: 800;
  width: 400px;
  margin-bottom: 20px;
}
.gv-scuoladigitale-ricerca-buttons {
  margin-top: 10px;
}
.gv-scuoladigitale-ricerca-buttons img {
  float: left;
  margin-right: 150px;
  margin-top: 10px;
}
</style>

<style>
.el-tree-node__label {
  font-size: 12px !important;
}

.el-tabs__item.is-active {
  color: #24386c !important;
}

.el-tabs__header {
  margin: 0 0 5px !important;
}
</style>



